// Document
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html {
    font-size: $parentRem;
    @include lg(font-size, $parentRemLg);
    @include md(font-size, $parentRemMd);
    @include sm(font-size, $parentRemSm);
    @include xs(font-size, $parentRemXs);
}

body {
    position: relative;
}

div {
    display: block;
}

span {
    display: inline-block;
}

img {
    display: inline-block;
    max-width: 100%;
}

// Interaction
a {
    display: inline-block;
    text-decoration: none;
    cursor: pointer;
}

button {
    outline: none;    
    cursor: pointer;    
}

select {}

// Forms
form {}

input {}

textarea {}

output {}

input[type="checkbox"] {}

input[type="radio"] {}

input[type="submit"],
input[type="button"] {
    outline: none;
    cursor: pointer;
}

// Lists
ul,
menu,
dir {
    display: block;
    list-style-type: disc;
    -webkit-margin-before: 0;
    -webkit-margin-after: 0;
    -webkit-margin-start: 0;
    -webkit-margin-end: 0;
    -webkit-padding-start: 0;
}

ul {
    list-style-type: none;
}

ol {}

li {
    list-style: none;
}

// Tables
caption {}

table {}

thead {}

tbody {}

tr {}

th {}

td {}

// Typography
h1,
h2,
h3,
h4,
h5,
h6,
p {
    -webkit-margin-before: 0;
    -webkit-margin-after: 0;
    -webkit-margin-start: 0;
    -webkit-margin-end: 0;
}

h1 {
    display: block;
    font-family: $firstFont;
    color: $darkColor;
    font-size: rem(60px);
    font-weight: 700;
    font-style: normal;
    line-height: 1.6;
    text-decoration: none;
    text-transform: none;
}

