p {
    display: block;
    font-family: $secondFont;
    color: $darkColor;
    font-size: rem(18px);
    font-weight: 400;
    font-style: normal;
    line-height: 1.6;
    text-decoration: none;
    text-transform: none;
}

blockquote {
    display: block;
    font-family: $secondFont;
    color: $darkColor;
    font-size: rem(18px);
    font-weight: 400;
    font-style: normal;
    line-height: 1.6;
    text-decoration: none;
    text-transform: none;
    background-color: rgba(0, 0, 0, 0.2);
}